@import 'config';
@import 'utilities';

.title-bar {
  display: flex;
  justify-content: space-between;
  @include set-bg-tbar($tbar-color);
  width: 80vw;
  margin: 2vw auto 0 auto;
  height: 2vh;
  min-height: 22px;
  background-color: $tbar-color;
  border: solid black;
  border-bottom: none;
  border-radius: 8px 8px 0 0;
  font-weight: bolder;
  padding: 8px;
  #max {
    margin: auto 18px;
    &:hover {
      cursor: pointer;
    }
  }
  #bar {
    margin: auto 18px;
  }
}

#do-p-display {
  .scroller.max-previewer {
    height: 92vh;
  }
  .scroller.norm-previewer {
    height: 57vh;
  }
}
#no-p-display {
  display: none;
}

#do-e-display {
  .scroller.max-editor {
    height: 92vh;
  }
  .scroller.norm-editor {
    height: 27vh;
  }
}
#no-e-display {
  display: none;
}