.scroller {
  display: block;
  resize: none;
  width: 80vw;
  margin: 0 auto 0 auto;
  padding:8px;
  border: solid;
  border-top: none;
  border-radius: 0 0 8px 8px;
  background-color: $bg-color;
  overflow-x: auto;
  overflow-y: auto;
  text-align:justify;
  &:focus {
    outline: none;
  }
  img {
    max-width: 90%;
  }
  pre {
    background: white;
  }
  code {
    background: white;
  }
}

.scroller::-webkit-scrollbar-track {
  border: 1px solid #000;
  padding: 2px 0;
  background-color: $bg-color;
}
.scroller::-webkit-scrollbar {
  width: 10px;
}
.scroller::-webkit-scrollbar-thumb {
  border-radius: 10px 0 10px 10px;
  box-shadow: inset 0 0 6px rgba(0,0,0,.3);
  background-color: $tbar-color;
  border: 1px solid #000;
}