.scroller {
  display: block;
  resize: none;
  width: 80vw;
  margin: 0 auto 0 auto;
  padding: 8px;
  border: solid;
  border-top: none;
  border-radius: 0 0 8px 8px;
  background-color: #7FFFD4;
  overflow-x: auto;
  overflow-y: auto;
  text-align: justify;
}

.scroller:focus {
  outline: none;
}

.scroller img {
  max-width: 90%;
}

.scroller pre {
  background: white;
}

.scroller code {
  background: white;
}

.scroller::-webkit-scrollbar-track {
  border: 1px solid #000;
  padding: 2px 0;
  background-color: #7FFFD4;
}

.scroller::-webkit-scrollbar {
  width: 10px;
}

.scroller::-webkit-scrollbar-thumb {
  border-radius: 10px 0 10px 10px;
  -webkit-box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.3);
          box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.3);
  background-color: #0cc46b;
  border: 1px solid #000;
}

.title-bar {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: justify;
      -ms-flex-pack: justify;
          justify-content: space-between;
  background-color: #0cc46b;
  color: 5F9EA0;
  width: 80vw;
  margin: 2vw auto 0 auto;
  height: 2vh;
  min-height: 22px;
  background-color: #0cc46b;
  border: solid black;
  border-bottom: none;
  border-radius: 8px 8px 0 0;
  font-weight: bolder;
  padding: 8px;
}

.title-bar #max {
  margin: auto 18px;
}

.title-bar #max:hover {
  cursor: pointer;
}

.title-bar #bar {
  margin: auto 18px;
}

#do-p-display .scroller.max-previewer {
  height: 92vh;
}

#do-p-display .scroller.norm-previewer {
  height: 57vh;
}

#no-p-display {
  display: none;
}

#do-e-display .scroller.max-editor {
  height: 92vh;
}

#do-e-display .scroller.norm-editor {
  height: 27vh;
}

#no-e-display {
  display: none;
}
